                <p>Partnership structure - a group of community members who are in team relationships formed in the process of joining new members to the community at the invitation of existing members. The partnership structure in relation to each individual participant can be higher and lower.</p>

                <p>The parent structure of a participant is a sequence of participants who are invitees to each other in different generations. At the same time, the direct curator (inviter) of this participant is considered his curator and is the first level of the higher partner structure of this participant.</p>

                <p>A member's downline is a group of members formed as a result of new members joining the community at the personal invitation of this member, as well as at the invitation of his personally invited members, etc. at the invitation of any other participants for whom he is a superior curator of any level.</p>

                <p>A place in the partner structure - is a participant account (a software tool for electronic identification of a participant), allowing him to receive financial assistance from other participants in accordance with the rules of the community, in the amount and on the conditions described in clause 4 of this agreement. A new member's account is generated automatically after registration.</p>

                <p>Personal account - a web-interface, access to which is made by login and password, to receive statistical information and other information about the services of the Contractor.</p>

                <p>The platform is a matrix that works on the principle of a referral program to build a logical sequence of participants after registration, according to the marketing plan.</p>

                <p>A marketing plan implies control over the algorithm of actions of the system processes, and the necessary budget for participating in a particular platform.  </p>

                <p class="header">2. Registration in the community</p>

                <p>For registration, the Participant undertakes to provide reliable and complete information about himself in the registration form posted on the page <a href="https://dreamoflives.com">https://dreamoflives.com</a> The Contractor reserves the right at any time to require the participant to confirm the data specified during registration, and to request supporting documents in this regard. The participant's personal information is processed and stored in a mode that ensures its confidentiality in accordance with the provisions of applicable law. After registering and activating the platform, the participant gets access to statistical data management functions and other services, which are accessed from the Participant's Personal Account.</p>

                <p class="header">3. Giving and receiving financial assistance</p>

                <p>After registration in the community, the participant is invited to provide financial assistance free of charge in the amount and on the terms, according to the marketing plan. After the provision of primary financial assistance, the platform is activated for the participant. Only members with activated platforms can receive financial assistance from other members of the Community.</p>

                <p>3.1. A participant who has not bought the platform does not have the opportunity to sell it. Money transfers sent to the participant for this platform are counted as “Lost Profits” and go to the upline curator as donations on behalf of the original sender.</p>

                <p>3.2. Return of financial assistance</p>

                <p>Amounts transferred by the participant in the account of gratuitous financial assistance are non-refundable.</p>

                <p class="header">4. Rules for the provision of financial assistance</p>

                <p>Donations / provision of financial assistance to other members of the community is made in the amount and on the terms, according to the marketing plan of the Community. All provision of financial assistance in the community is carried out exclusively by submitting an order for transactions through a special interface of the Member's Personal Account.</p>

                <p class="header">5. Rights and obligations</p>

                <p>5.1. Rights and obligations of the Contractor</p>

                <p>5.1.1. The Contractor undertakes to ensure the round-the-clock functioning of the <a href="https://dreamoflives.com">https://dreamoflives.com</a> website and the Participants' Personal Accounts, with the exception of the time of planned technical work with a total duration of no more than 24 hours per month.</p>

                <p>5.1.2. The Contractor has the right at any time to demand from the participant confirmation that he has reached 18 years of age, confirmation of the personal data specified by him during registration, and also suspend the execution of this agreement in relation to the participant who did not provide such confirmation within 5 days after sending the request to him (by notification to personal account, by e-mail and (or) sms - system.</p>

                <p>5.1.3. The Contractor has the right to suspend the maintenance of the participant's personal account in case of violation of the rules of this agreement by the participant.</p>

                <p>5.2. Rights and obligations of a participant</p>

                <p>5.2.1. The participant undertakes:</p>

                <p>- To register in the community only after he is 18 years old.</p>

                <p>- Provide true contact and personal details when registering.</p>

                <p>- Ensure the safety of the login and password for access to the Personal Account in the system, do not disclose them to third parties.</p>

                <p>- Do not create more than one Personal account (registration) in the community in your name.</p>

                <p>- Do not spread slanderous statements and other negative statements about the community (management, participants and the project as a whole).</p>
                
                <p>- Do not impersonate an employee, owner or authorized representative of the Contractor's company (if not), any representation must be limited to the name "Contributor".</p>

                <p>- When creating Internet resources and promotional materials, do not use the design or design elements of the official site <a href="https://dreamoflives.com">https://dreamoflives.com</a> , with the exception of promotional materials provided by the Contractor, the list of which is in the participant's personal account.</p>

                <p>- Independently pay taxes on income arising from participation in the community in accordance with the current tax legislation of the country of which he is a citizen.</p>

                <p>5.2.2. The participant is strictly prohibited from:</p>

                <p>- Motivate new members financially (pay cash rewards to new members for registering in the community).</p>

                <p>- Offer and sell to new members tariffs at a cost different from those specified in the Community's marketing plan.</p>

                <p>- Create affiliated chains of 2 or more successive registrations.</p>

                <p>- The use of profanity, swearing, humiliating sayings in speech, without even considering what form they were in, in what form and to whom they were addressed. And even when changing letters by symbols.</p>

                <p>5.2.3. The participant has the right:</p>

                <p>- Produce information to other persons with a view to attracting them to participate in the Community.</p>

                <p>- Create your own websites and post information about the community on them in order to attract new members to the community, as well as use any (without restrictions) resources on the Internet for these purposes.</p>

                <p>- Send the Contractor your wishes and feedback in order to improve the service.</p>

                <p>- At any time, opt out of further participation in the community.</p>

                <p>5.3. System actions in relation to blocked accounts</p>

                <p>In case of blocking the Personal account (cabinet) of the participant, which arose as a result of violation of the terms of this agreement, the following rules apply to such participant:</p>

                <p>- During the blocking of the Personal Account, the participant is not allowed to enter the Personal Account.</p>

                <p>- Participant invitation links are inactive.</p>

                <p>- All money transfers sent to a participant whose Personal Account is blocked are counted as Lost Profits and are credited to the account of the "Dream of Life" project as donations on behalf of the original sender.</p>

                <p class="header">6. Use of personal data of participants</p>

                <p>6.1. The participant agrees that the personal data specified by him (login, name, surname, e-mail, accounts in social networks and other data) will be available for viewing by other members of the community.</p>

                <p>6.2. The Contractor has the right to inform the participants via the specified contacts (e-mail mailing, sms mailing, communication via Skype, telephone, telegram, accounts in social networks).</p>

                <p>6.3. The Contractor has the right to place a participant's review on the <a href="https://dreamoflives.com">https://dreamoflives.com</a> website indicating his full name, after the participant sends such a review to the community.</p>

                <p class="header">7. Responsibility</p>

                <p>The Contractor is solely responsible for the functioning of the Personal Account. The Contractor is not responsible for any violations or restrictions in the operation of payment systems used to send funds to the personal accounts of the participants.</p>

                <p class="header">8. Liability for fraudulent activities</p>

                <p>In case of detection of fraudulent actions on the part of the participant (attempts to conduct fraudulent operations on payment systems, fraudulent receipt of money transfers from users, distortion of the official marketing plan, attempts to hack the Participants' Personal Accounts and (or) steal passwords), the Contractor has the right to block access to the Personal offender's office and terminate its service.</p>

                <p class="header">9. Force majeure</p>

                <p>The Contractor is released from liability for partial or complete failure to fulfill obligations under this Agreement if it was the result of force majeure circumstances, namely: fire, earthquake, flood, export or import embargo, sanctions of authorities and (or) administrative bodies of various instances, introduction of new bills, malicious attacks on the Internet resource <a href="https://dreamoflives.com">https://dreamoflives.com</a> , etc., if these circumstances directly affected the implementation of the provisions of this Agreement.</p>

                <p class="header">10. Rights to Amend This Agreement</p>

                <p>The Contractor reserves the right to correct and amend this agreement unilaterally, about which it undertakes to inform the participants by posting information on the website <a href="https://dreamoflives.com">https://dreamoflives.com</a> . In case of disagreement with the changes, the participant has the right to refuse to execute this agreement and stop using the service provided by the Contractor. In this case, the amounts previously paid by the participant in the form of financial assistance will not be returned.</p>

                <p>Any participant has permanent open access to this agreement at <a href="https://dreamoflives.com/#/agreement">https://dreamoflives.com/#/agreement</a></p>


